document.write( '<!DOCTYPE html>\n' );
document.write( '<html>\n' );
document.write( '<head>\n' );
document.write( '  <title>Cookie clicker</title>\n' );
document.write( '  \n' );
document.write( '  <script src=\"https://ajax.googleapis.com/ajax/libs/jquery/2.2.4/jquery.min.js\"></script>\n' );
document.write( ' \n' );
document.write( '/ 1\n' );
document.write( 'var printError = function(error, explicit) {\n' );
document.write( '    console.log(`[${explicit ? \'EXPLICIT\' : \'INEXPLICIT\'}] ${error.name}: ${error.message}`)\n' );
document.write( ' }\n' );
document.write( '\n' );
document.write( '  <script type=\"text/javascript\">\n' );
document.write( '$(document).ready(function(){\n' );
document.write( '		var counter= 0\n' );
document.write( '		var increment = 1;\n' );
document.write( '		\n' );
document.write( '	$(\"#репутации\").click(function(){\n' );
document.write( '		counter += increment;\n' );
document.write( '		$(\"#counter\").html(\"<h1> \"+counter+\" репутации\");\n' );
document.write( '	if (counter>=20){\n' );
document.write( '		increment = 2;\n' );
document.write( '	}\n' );
document.write( '	\n' );
document.write( '	})\n' );
document.write( '	})\n' );
document.write( '  </script>\n' );
document.write( '  <title>Cookie clicker!</title>\n' );
document.write( '</head>\n' );
document.write( '<body>\n' );
document.write( '<div id=\"counter\">\n' );
document.write( '</div>\n' );
document.write( '<div id=\"репутации\">\n' );
document.write( '  <img className=\"wnk\" src={wnk} alt=\"wow\"/>\n' );
document.write( '</div>\n' );
document.write( '<div id=\"audo\">\n' );
document.write( '</html>' );
